/**
 * About page  reduced copy, premium spacing
 * Rhythm: Cream  Paper  Olive  Cream  Paper  Maroon
 * Increased vertical spacing; clear H2 separation; grid where specified.
 */

:root {
  --mrc-about-section-padding: 7.5rem 0;
  --mrc-about-section-padding-mobile: 5rem 0;
  --mrc-about-hero-padding: 8rem 0 6rem;
  --mrc-about-hero-padding-mobile: 6rem 0 4.5rem;
  --mrc-about-cta-padding: 7.5rem 0;
  --mrc-about-cta-padding-mobile: 5rem 0;
}

/* ---------- Section spacing (design correction: more space between sections) ---------- */
.mrc-page-about .mrc-about-hero {
  padding: var(--mrc-about-hero-padding);
}

@media (max-width: 767px) {
  .mrc-page-about .mrc-about-hero {
    padding: var(--mrc-about-hero-padding-mobile);
  }
}

.mrc-page-about .mrc-two-col,
.mrc-page-about .mrc-about-why-anchor,
.mrc-page-about .mrc-about-experience,
.mrc-page-about .mrc-about-beyond {
  padding: var(--mrc-about-section-padding);
}

@media (max-width: 767px) {
  .mrc-page-about .mrc-two-col,
  .mrc-page-about .mrc-about-why-anchor,
  .mrc-page-about .mrc-about-experience,
  .mrc-page-about .mrc-about-beyond {
    padding: var(--mrc-about-section-padding-mobile);
  }
}

/* ---------- Hero (Cream, 70ch, large vertical padding) ---------- */
.mrc-about-hero {
  background-color: var(--mrc-bg);
}

.mrc-about-hero-inner {
  max-width: min(70ch, 100%);
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  padding-bottom: 2rem;
}

.mrc-about-hero-title {
  font-family: var(--font-serif);
  font-size: clamp(1.85rem, 4vw, 2.5rem);
  font-weight: 600;
  color: var(--mrc-text);
  margin: 0 0 1rem;
  line-height: 1.25;
}

.mrc-about-hero-subtext {
  font-size: 1.1rem;
  color: var(--mrc-text-muted);
  line-height: 1.6;
  margin: 0 0 0.5em;
  max-width: 55ch;
  margin-left: auto;
  margin-right: auto;
}

.mrc-about-hero-subtext:last-of-type {
  margin-bottom: 0;
}

.mrc-about-hero-divider {
  height: 3px;
  background-color: #E8C868;
  margin: 0 auto;
  max-width: min(70ch, 100%);
  border: 0;
}

.mrc-about-hero .mrc-about-hero-subtext-below {
  text-align: center;
  max-width: 55ch;
  margin: 2rem auto 0;
  display: block;
}

/* ---------- Intro (Paper, 60/40 when right column or placeholder; left max 60ch) ---------- */
.mrc-page-about .mrc-two-col {
  background-color: var(--mrc-surface);
  border-top: 1px solid rgba(42, 38, 26, 0.06);
}

.mrc-two-col-inner {
  max-width: min(75ch, 100%);
  margin-left: auto;
  margin-right: auto;
}

.mrc-two-col-has-right {
  max-width: 1100px;
  display: grid;
  grid-template-columns: 60fr 40fr;
  gap: 3.5rem;
  align-items: start;
}

.mrc-two-col-left {
  max-width: 60ch;
}

.mrc-two-col-has-right .mrc-two-col-left {
  max-width: none;
}

.mrc-two-col-placeholder {
  min-height: 240px;
}

@media (max-width: 767px) {
  .mrc-two-col-has-right {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .mrc-two-col-placeholder {
    min-height: 0;
    display: none;
  }
}

/* H2: significant margin above (avoid stacking without spacing contrast) */
.mrc-page-about .mrc-two-col-title {
  font-family: var(--font-serif);
  font-size: var(--mrc-h2-size);
  font-weight: 600;
  color: var(--mrc-text);
  margin: 0 0 1.25rem;
}

.mrc-page-about .mrc-two-col-p {
  font-size: 1rem;
  line-height: var(--mrc-body-line);
  color: var(--mrc-text);
  margin: 0 0 1.25em;
}

.mrc-page-about .mrc-two-col-p:last-child {
  margin-bottom: 0;
}

.mrc-two-col-img {
  max-width: 100%;
  width: 100%;
  height: auto;
  border-radius: 4px;
  opacity: 0.95;
}

/* ---------- Why This Exists (Olive anchor, 65ch, stacked statements, more white space) ---------- */
.mrc-about-why-anchor {
  background-color: var(--mrc-olive);
  color: var(--mrc-surface);
}

.mrc-about-why-inner {
  max-width: min(65ch, 100%);
  margin-left: auto;
  margin-right: auto;
}

.mrc-about-why-title {
  font-family: var(--font-serif);
  font-size: var(--mrc-h2-size);
  font-weight: 600;
  color: var(--mrc-surface);
  margin: 0 0 2.5rem;
}

.mrc-about-statement {
  font-size: 1.05rem;
  line-height: 1.65;
  color: var(--mrc-surface);
  opacity: 0.95;
  margin: 0 0 1.15rem;
}

.mrc-about-statement-accent {
  font-weight: 600;
  opacity: 1;
  margin-top: 0.25rem;
  margin-bottom: 1.15rem;
}

.mrc-about-why-list {
  list-style: none;
  padding: 0;
  margin: 1.25rem 0 1.5rem;
}

.mrc-about-why-list li {
  font-size: 1.05rem;
  line-height: 1.65;
  color: var(--mrc-surface);
  opacity: 0.95;
  margin-bottom: 0.6rem;
  padding-left: 1.25rem;
  position: relative;
}

.mrc-about-why-list li::before {
  content: "";
  position: absolute;
  left: 0;
  color: var(--mrc-mustard);
  opacity: 0.8;
}

.mrc-about-why-divider {
  height: 2px;
  background-color: var(--mrc-mustard);
  opacity: 0.7;
  margin: 2.5rem 0 2rem;
  max-width: 5rem;
  border: 0;
}

/* ---------- Experience (Cream, two-column equal cards) ---------- */
.mrc-about-experience {
  background-color: var(--mrc-bg);
}

.mrc-about-experience-inner {
  max-width: 1100px;
  margin-left: auto;
  margin-right: auto;
}

.mrc-about-section-title {
  font-family: var(--font-serif);
  font-size: var(--mrc-h2-size);
  font-weight: 600;
  color: var(--mrc-text);
  margin: 0 0 2.5rem;
}

.mrc-about-cards {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2.5rem;
}

@media (max-width: 767px) {
  .mrc-about-cards {
    grid-template-columns: 1fr;
  }
}

.mrc-about-card {
  padding: 2rem;
  background-color: var(--mrc-surface);
  border-radius: 4px;
  border-left: 4px solid var(--mrc-olive);
}

.mrc-about-card-title {
  font-family: var(--font-serif);
  font-size: var(--mrc-h3-size);
  font-weight: 600;
  color: var(--mrc-text);
  margin: 0 0 1rem;
}

.mrc-about-card-intro {
  font-size: 1rem;
  line-height: var(--mrc-body-line);
  color: var(--mrc-text);
  margin: 0 0 0.75rem;
}

.mrc-about-card-subhead {
  font-size: 1rem;
  font-weight: 600;
  color: var(--mrc-text);
  margin: 1.25rem 0 0.5rem;
}

.mrc-about-card-outro {
  font-size: 1rem;
  line-height: var(--mrc-body-line);
  color: var(--mrc-text-muted);
  font-style: italic;
  margin: 1.25rem 0 0;
}

.mrc-about-card-list {
  list-style: disc;
  padding-left: 1.25rem;
  margin: 0;
}

.mrc-about-card-list li {
  font-size: 1rem;
  line-height: 1.6;
  color: var(--mrc-text);
  margin-bottom: 0.5rem;
}

.mrc-about-card-list li:last-child {
  margin-bottom: 0;
}

/* ---------- Outside of Work (Paper, 65ch, two short paragraphs) ---------- */
.mrc-page-about .mrc-about-beyond {
  background-color: var(--mrc-surface);
  border-top: 1px solid rgba(42, 38, 26, 0.06);
}

.mrc-about-prose-inner {
  max-width: min(65ch, 100%);
  margin-left: auto;
  margin-right: auto;
}

.mrc-about-beyond .mrc-about-section-title {
  margin: 0 0 1.5rem;
}

.mrc-about-prose-p {
  font-size: 1rem;
  line-height: var(--mrc-body-line);
  color: var(--mrc-text);
  margin: 0 0 1.35em;
}

.mrc-about-prose-p:last-child {
  margin-bottom: 0;
}

/* ---------- Final CTA (Maroon, large vertical padding) ---------- */
.mrc-page-about .mrc-cta-section {
  padding: var(--mrc-about-cta-padding);
}

@media (max-width: 767px) {
  .mrc-page-about .mrc-cta-section {
    padding: var(--mrc-about-cta-padding-mobile);
  }
}

/* ---------- H2 spacing contrast (margin above) sitewide on About ---------- */
.mrc-page-about .mrc-about-section-title,
.mrc-page-about .mrc-about-why-title {
  margin-top: 0;
}

.mrc-page-about .mrc-about-beyond .mrc-about-section-title {
  margin-top: 0;
}

/* ---------- Page wrapper: no bottom padding so CTA meets footer ---------- */
.site-content:has(.mrc-page-about) {
  padding-bottom: 0;
}
